// 精准四舍五入及补零
const toFixedSHW = (number, len, pushZero) => {
	let num = parseFloat(number)
	let _this = isNaN(num) ? 0 : num
	let _len = len === undefined ? 0 : len - 0
	if (_len === 0) {
		return _this >= 0 ? Math.round(_this) : 0 - Math.round(Math.abs(_this))
	}
	let mathPow = Math.pow(10, _len)
	let _pushZero = pushZero === undefined ? true : pushZero
	let str = _this + ''
	let dot = str.indexOf('.')
	let _num = null
	let dotLen = dot + _len + 1
	if (dot !== -1 && str.substr(dotLen, 1)) {
		_this += 0.0000000001
		_num = Math.round(_this * mathPow) / mathPow + ''
	} else {
		_num = str
	}
	if (_pushZero) {
		dot = _num.indexOf('.')
		if (!~dot) {
			_num += '.'
			dot = _num.length - 1
		}
		// 获取当前小数点后位数,跟参数_len比较一下,如果不等用0补齐
		let dotLen = _num.length - (dot + 1)
		// 需要补0的个数
		let zeroLen = _len - dotLen
		if (dotLen < _len) {
			for (let i = 0; i < zeroLen; i++) {
				_num += '0'
			}
		}
	}
	return _num
}
// 判断空对象
const isEmptyObjSHW = obj => {
	for (let key in obj) {
		if (Object.prototype.hasOwnProperty.call(obj, key)) {
			return false
		}
	}
	return true
}

// 生成uuid
const createUUID = () => {
	let myDate = new Date()
	let myM = myDate.getMinutes()
	let myS = myDate.getSeconds()
	let myMS = myDate.getMilliseconds()
	let random = Math.round(Math.random() * 10000)
	return `fxCreateParams${myM}${myS}${myMS}${random}`
}
// 格式化日期
const setDate = (date) => {
	let y = date.getFullYear()
	let m = date.getMonth() + 1
	let d = date.getDate()
	m = m < 10 ? '0' + m : m
	d = d < 10 ? '0' + d : d
	return `${y}-${m}-${d}`
}
// 获取当月第一天
const getCurrentMonthFirst = () => {
	var date = new Date()
	var firstDay = new Date(date.getFullYear(), date.getMonth(), 1)
	return setDate(firstDay)
}
// 获取当月最后一天
const getCurrentMonthLast = () => {
	var date = new Date()
	var lastDay = new Date(date.getFullYear(), date.getMonth() + 1, 0)
	return setDate(lastDay)
}
// JSON解析
const JSONparse = (jsonStr, type) => {
	try {
		let obj = JSON.parse(jsonStr)
		return obj
	} catch (error) {
		console.log(error)
		if (type === 'object') {
			return {}
		}
		return undefined
	}
}

const utils = {
	toFixedSHW,
	isEmptyObjSHW,
	createUUID,
	setDate,
	getCurrentMonthFirst,
	getCurrentMonthLast,
	JSONparse
}
export default utils
