<template>
	<div id="app">
		<transition :name="transitionName" mode="out-in">
			<keep-alive include="orderDetails">
				<router-view ></router-view>
			</keep-alive>
		</transition>
	</div>
</template>
<script>
const pathHistory = []
export default {
	name: 'app',
	data () {
		return {
			transitionName: 'page-next'
		}
	},
	watch: {
		$route (to, from) {
			const len = pathHistory.length - 1
			if (len >= 0 && pathHistory[len] === to.path) {
				pathHistory.pop()
				this.transitionName = 'page-fade-in-left'
			} else {
				pathHistory.push(from.path)
				this.transitionName = 'page-fade-in-right'
			}
		}
	}
}
</script>
<style lang="stylus">
#app {
	text-align center
	height:100%
}
.page-fade-in-right-enter-active {
  animation: fadeinR .5s;
}
.page-fade-in-left-enter-active {
  animation: fadeinL .5s;
}
@keyframes fadeinR {
  0% {
	opacity: 0;
	transform: translateX(100%);
  }
  100% {
	opacity:1;
	transform:translateX(0);
  }
}
@keyframes fadeinL{
  0% {
	opacity: 0;
	transform: translateX(-100%);
  }
  100% {
	opacity: 1;
	transform: translateX(0);
  }
}
[v-cloak] {
	display: none;
}
</style>

